import { AuthorizationStatus, GameType } from './const';
export var isArtistAnswerCorrect = function (question, userAnswer) { return userAnswer === question.song.artist; };
export var isGenreAnswerCorrect = function (question, userAnswer) { return userAnswer.every(function (answer, index) { return answer === (question.answers[index].genre === question.genre); }); };
export var isAnswerCorrect = function (question, answer) {
    if (question.type === GameType.Artist && typeof answer === 'string') {
        return isArtistAnswerCorrect(question, answer);
    }
    if (question.type === GameType.Genre && Array.isArray(answer)) {
        return isGenreAnswerCorrect(question, answer);
    }
    return false;
};
export var isCheckAuth = function (authorizationStatus) { return authorizationStatus === AuthorizationStatus.Unknown; };
